.navContainer {
    display: flex;
    top: 0;
    align-items: center;
    padding: 10px 0 10px 0;
    /* background: blue; */
    width: 100%;;

}

.navContainerPadding {
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: 1024px;
    max-width: 102.4rem;
    width: 90%;
    flex-wrap: wrap;
    margin-left: auto;
    margin-right: auto;
}

.navbar {
    flex: 1;
    display: flex;
    max-height: 30px;
    font-size: 20px;
    font-weight: 600;
    align-items: center;

    -webkit-box-align: center;
    -ms-flex-align: center;
    -webkit-box-pack: justify;
    -ms-flex-pack: justify;
}

.logo {
    padding-right: 25px;
}


.listMenu {
    width: fit-content;
    display: flex;
    padding: 10px;
    font-weight: 500;
    font-size: 16px;
}

.listicles {
    display: flex;
    text-decoration: none;
    list-style-type: none;
    gap: 60px;
}

.list {
    text-decoration: none;
}



/*=================== MEDIA QUERIES (Medium devices) ===================*/
@media screen and (max-width: 1024px) {

}

/*=================== MEDIA QUERIES (Small devices) ===================*/
@media screen and (max-width: 768px) {
    .navContainer {
        padding-bottom: 15px;
        padding-bottom: 1.5rem;
        padding-top: 20px;
        padding-top: 2rem;
    }
    .listMenu {
        display: none;
    }
}